import { graphql } from "gatsby";

import React from "react";
import CommonHead from "../layout/Head";
import ProductDetailsPage from "../layout/pages/ProductDetailsPage";
import JsonLd from "../layout/jsonLd";
import { baseUrl, createProductJsonLd, homeJsonLd } from "../seo/jsonLd";
import { parseAllGoogleSpreadsheetPetitsAnimauxProductsResponse } from '../graphql/products';

const PageTemplate = (props) => {
  const products = parseAllGoogleSpreadsheetPetitsAnimauxProductsResponse(props.data);

  if (products.length === 0) {
    return <>Cannot find the product.</>
  }

  const product = products[0];
  return (
    <>
      <ProductDetailsPage product={product} />
      <JsonLd data={createProductJsonLd(product)} />
      <JsonLd data={homeJsonLd} />
    </>
  )
};

export function Head(props) {
  const products = parseAllGoogleSpreadsheetPetitsAnimauxProductsResponse(props.data);

  let title = "Cannot find the product";
  if (products.length > 0) {
    title = products[0].title;
  }

  let image;
  if (products[0].gatsbyImages) {
    image = `https://crochetnest.com${products[0].gatsbyImages[0].images.fallback.src}`;
  }

  const data = {
    page: {
      name: title,
      description: products[0].whyYoullLove ? products[0].whyYoullLove : products[0].description,
      image,
      keywords: products[0].tags.join(',')
    }
  };

  const canonicalUrl = `${baseUrl}product/${products[0].urlSuffix}/`;
  return <CommonHead {...props} data={data} canonicalUrl={canonicalUrl} />;
}

export const query = graphql`
query PetitsAnimaux($id: String!) {
  allGoogleSpreadsheetPetitsAnimauxProducts(filter: {id: {eq: $id}}) {
    edges {
      node {
        id
        title
        description
        price
        etsyAffiliateLink
        prices
        currencyCode
        quantity
        tags
        materials
        images
        urlSuffix
        whyYoullLove
        reviews
        tags
        image1Gatsby {
          publicURL
          childImageSharp {
            gatsbyImageData(height: 300)
          }
        }
        image2Gatsby {
        publicURL
          childImageSharp {
            gatsbyImageData(height: 300)
          }
        }
        image3Gatsby {
          publicURL
          childImageSharp {
            gatsbyImageData(height: 300)
          }
        }
        image4Gatsby {
          publicURL
          childImageSharp {
            gatsbyImageData(height: 300)
          }
        }
        image5Gatsby {
          publicURL
          childImageSharp {
            gatsbyImageData(height: 300)
          }
        }
        image6Gatsby {
          publicURL
          childImageSharp {
            gatsbyImageData(height: 300)
          }
        }
        image7Gatsby {
          publicURL
          childImageSharp {
            gatsbyImageData(height: 300)
          }
        }
        image8Gatsby {
          publicURL
          childImageSharp {
            gatsbyImageData(height: 300)
          }
        }
        image9Gatsby {
          childImageSharp {
            gatsbyImageData(height: 300)
          }
        }
        image10Gatsby {
          childImageSharp {
            gatsbyImageData(height: 300)
          }
        }
        length
        width
        height
      }
    }
  }
}
`;

export default PageTemplate;